@import url('https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700&display=swap');





body{
    margin:0px;
    padding: 0px;
    background-color: #000000;
    font-family: 'Lato', sans-serif;
    max-width: 100%;
    overflow-x: hidden;

}
*{
    box-sizing: border-box;
}
ul{
    list-style: none;
}
a{
    text-decoration: none;
}
button{
    outline: none;
    border: none;
}
input{
    outline: none;
    border: none;
}









        
        
       
       

   
        
       
        
        

           