#about{

	width:100%;
	height: 120vh;
    align-items: center;
    /* justify-content: space-between; */
    box-sizing: border-box;
    display: flex;
    cursor:grab ;
    padding: 5px 2.5% 0px 2.5%;
    position: relative;
    overflow: hidden;
    margin: 2%  0;

    
}

.about-text{
    /* width: 45%; */
    
    display: flex;
 	
   
}
.event{
    min-height:10rem ;
    min-width:20rem;
    padding:40px;

}
.event img{
    width: 100%;
    height:100%;
    border-radius: 2rem;
    box-shadow: 5px 10px 30px rgba(255, 2, 2, 0.336);

}
 .about-text h1{
	font-size: 3rem;
	color:#FF1414;
    font-weight: 500;
    margin: 0px;
	padding: 0px;
    
}
.about-text p{
    width: 80%;
	font-size: 1.2rem;
	color:#FCFFE7;
	
}
.about-text button{
    margin-top: 20px;
	width: 140px;
	height: 45px;
	border-radius: 10px;
	border: none;
    outline: none;
    color: #ffffff;
	background-color:#FF1414;
}
.about-text button:hover{
    box-shadow: 5px 10px 30px rgba(255, 2, 2, 0.336);
	background-color: rgb(0, 0, 0);
    transition: all ease 0.3s;
    color: #ffffff;
} 
 .about-image{
    width: 50%;
   
}
.about-image img{
  width: 600px;
} 




@media only screen and (max-width:1190px){
	
	
    
	.about-image img{
		height:400px;
    }
}
@media(max-width:970px){

.about-image{
    display:none;
}
#about{
    justify-content: center;
    padding-top: 0px;
    text-align: center;
    
    margin-top: 0px;
}
.about-text{
    width: 90%;
}
.about-text h1{
    font-size:4rem;

}
.about-text p{
    width: 100%;
}
}


.about-text ul {
    display: flex;
    list-style: none;
    height: 300px;
    overflow-x: scroll;
    padding: 20px 0;
    flex: 0 0 600px;
    margin: 0 auto;
  }
  
  ::-webkit-scrollbar {
    height: 5px;
    width: 5px;
    background: #fff3;
  
  }
  
  ::-webkit-scrollbar-thumb {
    background: var(--accent);
   
  }
  
  ::-webkit-scrollbar-corner {
    background: #fff3;
  }
  
  .about-text li {
    flex: 0 0 200px;
    background: var(--accent);
    margin: 0 20px 0 0;
  }
  
  li:last-of-type {
    margin: 0;
  }
   


