

.name{
    text-align: center;
	font-family: 'Montserrat';
	width: 900px;
	position: absolute;
    left: 50%;
	top: 55%;
	transform: translate(-50%,-55%);
}
.name span{
    color: #FF1414;
}
.name .details{
    font-size: 1.5rem;
    color: #fFF6C3;
}
.name .details span{
     color: #FFF6C3;
}
#highlight{
    position: relative;
}
#highlight::before {
    /* Highlight color */
    background-color: #850000;

    content: "";
    position: absolute;
    width: calc(100% + 4px);
    height: 60%;
    left: -2px;
    bottom: 0;
    z-index: -1;
    transform: rotate(-2deg);
  }
  

.name h2{
	font-family:'Lato';
	font-size: 2rem;
	margin:0px;
	letter-spacing: 2px;
	color:#FFF6C3;
}
.name h1{
	font-family:'Lato';
	font-size: 1.9rem;
	margin:0px;
	letter-spacing: 2px;
	color:#FFF6C3;
}


.header-btns{
    display: flex;
    margin-top: 40px;
    margin-left: 40%;
}

.header-btn{
	width:160px;
	height: 50px;
	display: flex;
	justify-content: center;
    align-items: center;
    font-size: 1.4rem;
    background-color: #FF1414;
    box-shadow: 5px 10px 30px rgba(255, 2, 2, 0.336);
    border-radius: 5px;
	color:#ffffff;
}
.header-btn:hover{
	background-color: transparent;
    transition: all ease 0.5s;
    color: #ffffff;
    border: 2px solid #FF1414;
}

.carousel .thumbs-wrapper {
    display: none;
}
.carousel .control-dots{
    display: none;
}
.carousel.carousel-slider .control-arrow {
display: none;
}
.carousel-status{
    display: none;
}


@media only screen and (max-width:1100px){

.name{
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    text-align: center;
    
    
}
.name h2{
    font-size: 1.2rem;
}
.name h1{
    font-size: 1.8rem;
}
.name .details{
    font-size: 1.2rem;
}

.header-btn{
    width: 160px;
    height:40px;
}
}
@media(max-width:970px){
.name{
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);

}

.name h2{
    font-size: 0.8rem;
}
.name h1{
    font-size: 1rem;
}
.name .details{
    font-size: 1rem;
}
.header-btn{
    margin-left: 8%;
    margin-top: 20px;
    width: 100px;
    height:40px;
   
}
}

@media only screen and (max-width: 768px){
	

    .name h2{
        font-size: 1.2rem;
    }
    .name h1{
        font-size: 1.5rem;
    }
    .name .details{
        font-size: 1.1rem;
    }
    .header-btns{
        margin-left: 45%;
        width: 90px;
        height:30px;
        
    
    }
}
