#contact{
    width:100%;
    display:flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
#contact h1{
    color: #fff;
    font-size: 3rem;
}
#contact p {
    width:400px;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 40px;
    color: #c5c5c5;
    
}

#contact .dial{
    display: flex;
    flex-direction: row;
       color: #c5c5c5;
}

.copyright{
    font-size: 0.8rem;
    padding: 8px;
}

